// è¿æ¥é’±åŒ…ï¼ˆéœ€è¦ç”¨æˆ·ç‚¹å‡»è§¦å‘ï¼‰
const connectWallet = async () => {
    if (!web3) {
        throw new Error('æœªæ£€æµ‹åˆ°é’±åŒ…ï¼Œè¯·å°è¯•ä½¿ç”¨å°ç‹ç‹¸é’±åŒ…');
    }

    try {
        // æ·»åŠ è°ƒè¯•æ—¥å¿—
        let chainId = await web3.eth.getChainId();
        chainId = Number(chainId); // è½¬æ¢ä¸º number
        console.log('å½“å‰é“¾ID:',
            `ç±»å‹: ${typeof chainId}`,
            `å€¼: ${chainId}ï¼ˆåè¿›åˆ¶ï¼‰`,
            `ç›®æ ‡é“¾ID: ${SEPOLIA_CHAIN_ID}ï¼ˆåè¿›åˆ¶ï¼‰`
        );


        // 1. è¯·æ±‚è´¦æˆ·è¿æ¥
        const accounts = await window.ethereum.request({
            method: 'eth_requestAccounts'
        });

        // 2. æ£€æŸ¥ç½‘ç»œ

        if (chainId !== parseInt(SEPOLIA_CHAIN_ID, 16)) {
            try {
                await window.ethereum.request({
                    method: 'wallet_switchEthereumChain',
                    params: [{ chainId: SEPOLIA_HEX_ID }] // ä½¿ç”¨åå…­è¿›åˆ¶
                });
            } catch (switchError) {
                // 4902é”™è¯¯è¡¨ç¤ºè¯¥é“¾å°šæœªæ·»åŠ åˆ°é’±åŒ…
                if (switchError.code === 4902) {
                    await addSepoliaNetwork();
                } else {
                    throw new Error('è¯·æ‰‹åŠ¨åˆ‡æ¢åˆ°Sepoliaæµ‹è¯•ç½‘');
                }
            }
        }

        return accounts[0];
    } catch (error) {
        console.error('è¿æ¥å¤±è´¥:', error);
        throw error;
    }
};

// æ·»åŠ Sepoliaç½‘ç»œé…ç½®
const addSepoliaNetwork = async () => {
    await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [{
            chainId: SEPOLIA_HEX_ID, // æ”¹ä¸ºåå…­è¿›åˆ¶æ ¼å¼
            chainName: 'Sepolia Test Network',
            nativeCurrency: {
                name: 'Sepolia ETH',
                symbol: 'ETH',
                decimals: 18
            },
            rpcUrls: ['https://rpc.sepolia.org'],
            blockExplorerUrls: ['https://sepolia.etherscan.io']
        }]
    });
};


// è·å–å½“å‰è´¦æˆ·
const getAccount = async () => {
    const accounts = await web3.eth.getAccounts();
    return accounts[0] || null;
};



// ä¿®æ”¹ç½‘ç»œå˜æ›´ç›‘å¬
const listenNetworkChange = (callback) => {
    window.ethereum?.on('chainChanged', (hexChainId) => {
        const decimalChainId = parseInt(hexChainId, 16);
        console.log('ç½‘ç»œå˜æ›´äº‹ä»¶:',
            `åŸå§‹å€¼: ${hexChainId}ï¼ˆç±»å‹: ${typeof hexChainId}ï¼‰`,
            `è½¬æ¢å: ${decimalChainId}ï¼ˆç±»å‹: ${typeof decimalChainId}ï¼‰`
        );
        callback(decimalChainId);
    });
};
// è·å–å·²åˆå§‹åŒ–çš„web3å®ä¾‹
